<template>
   
    <div class="wrapper">
      <div class="title">
          我的全部购物车
      </div>
      <div class="cart" 
      v-for="(cart,key) in list " 
      :key="key"
      @click="() => handecartclice(key)">
        <div className="cart__title">{{cart.shopname}}</div>
         <div class="cart__item" v-for=" (product,innerkey) in cart.productlist " :key="innerkey">
            <img class="cart__image" :src="product.imgUrl" />
            <div class="cart__content">
                <p class="cart__content__title">{{product.name}}</p>
                <p class="cart__content__price">
                    <span class="yen">&yen;</span>{{product.price}} x {{product.count}}
                <span class="cart__content__total"> <span class="yen">&yen;</span>{{(product.price * product.count).toFixed(2) }}</span>
                </p>
                  
            </div>
         </div>
         <div class="cart__total">共计{{cart.total}}件</div>
      </div>
      <div class="empty"
      v-if="Object.keys(list).length === 0">
      暂无购物数据
      </div>
    </div>
    <docker :currentindex="1"/>

</template>


<script>
 import docker from '../../components/docker.vue'
 import {useRouter} from 'vue-router'
 
 export default {
      name:'orderlist',
      components:{docker},
      setup() {
       const list = JSON.parse(localStorage.cartList||'[]')
    //    计算购物车总件数逻辑
       for (let i in list){
        const cart = list[i]
        const productlist = cart.productlist;
        let total = 0;
        for(let j in productlist){
           const product = productlist[j];
           total += product['count']
        }
        cart.total = total;
       }
    //    处理点击
       const router = useRouter();
       const handecartclice = (key)=>{
        router.push(`/orderconfire/${key}`)

       }
          return { list,handecartclice}
      }
  }
</script>

<style lang="scss" scoped>
    @import '../../style/mixins.scss';
  .wrapper{
    overflow:auto;
    position: absolute;
    left: 0;
    top: 0;
    bottom: .5rem;
    right: 0;
    background: rgba(248,248,248);
  }
  .title{
      font-size: 0.16rem;
      background: #fff;
      color: #333;
      line-height: 0.44rem;
      text-align: center;
  }
  .cart{
    padding-bottom: .16rem;
    margin: .16rem;
    background: #fff;
    &__title{
        padding: .16rem;
        line-height: .22rem;
        font-size: .16rem;
        color: #333;
        @include ellipsis;
    }
    &__item{
        display: flex;
        padding: 0 .16rem .16rem .16rem;
      
        
    }
    &__image{
            width: 0.46rem;
            height: 0.46rem;
            margin-right: .16rem;
        }
        &__content{
            flex: 1;
            .yen{
                font-size: .12rem;
            }
            &__title{
                margin: 0;
                line-height: .2rem;
                font-size: .14rem;
                color: #333;
                @include ellipsis;
            }
            &__price{
                margin: 0;
                font-size: .14rem;
                color: #e93b3b;
            }
            &__total{
                float: right;
                color: #000;
            }
        }
        &__total{
            line-height: .28rem;
            margin: 0 .16rem ;
            color: #999;
            font-size: .14rem;
            text-align: center;
        }
       
  }
  .empty{
            text-align: center;
            line-height: .5rem;
            margin-top: .5rem;
            font-size: .18rem;
            color: #999;
        }

</style>
